import React from "react";

function Matopeli (){
    if(window.innerWidth >= 1024){
        return(
            <>
                <p>Ensimminen oma projekti oli nostalginen matopeli vrillisen ja valmistui alkukevll 2023. (Pelataksesi klikkaa peliruutua ja ohjaa nuolinppimill). Madon vauhti kasvaa asteittain.</p><p>Harjoittelun kohteena: Javascript, HTML, CSS</p>
                <iframe title="Matopeli" src="CrazyWormGameFinal/index.html"></iframe>
            </>
        )
    }else if (window.innerWidth < 1024){
        return (
            <>
            <p>Ensimminen oma projekti oli nostalginen matopeli vrillisen ja valmistui alkukevll 2023. Peli voi kokeilla tietokoneen nppimistll, yli 1024px:i leveill nytill.</p><p>Harjoittelun kohteena: Javascript, HTML, CSS</p>
            <img src="img/wormGameImg.png" id="wormGameImg" class="box_shadow" alt="gameplay"/>
        </>
        )
    }
}

export default Matopeli;