import React from "react";

function MeNow (){
    return(
        <>
            <p>Kipin koodaamiseen syttyi sattumalta vuonna 2019, ja siit lhtien palo on vain yltynyt. Itsens haastaminen, uuden opettelu ja ongelmien ratkominen on juuri tllaiselle jatkuvia haasteita kaipaavalle tarkoitettua. Koodaaminen on vlill hermoja raastavaa, mutta se tunne joka tulee onnistumisesta, on todellakin sen arvoinen!</p>
            <p>Tll hetkell opiskelen tietojenksittely Hmeen ammattikorkeakoulussa. Yleisi opintoja on kasassa toukokuussa 2024 juuri ja juuri alle 130 opintopistett, joten opinnot teorian osalta alkaa olla loppusuoralla. Ensi syksyn jljell olisi en hieman teoriaa, joten sen suorittaminen onnistuisi harjoittelun ohessa. Parhaillaan etsinkin harjoittelupaikkaa syksyksi 2024, ajankohdan suhteen olen kuitenkin joustava. </p>
            <p>Tyskentelen parhaillaan tysipivisesti opintojen ohessa teollisuudessa kondensaattorin valmistuksessa ja tarkoituksena on suorittaa alanvaihto kohti unelma-ammattiani koodaajana mahdollisimman pian.</p>
        </>

    );
}

export default MeNow;